<template>
    <v-card class="mx-auto my-12" max-width="500">
        <template slot="progress">
            <v-progress-linear color="deep-purple" height="10" indeterminate></v-progress-linear>
        </template>


        <v-card-title justify-right >
            <v-col cols="12" sm="6" md="6"   justify="right">
                            <v-text-field v-model="detalleEnsayo._id" label="Estado ID"></v-text-field>
                        </v-col>
        </v-card-title>

        <v-card-text>
            <v-row align="center" class="mx-0">
                <v-col cols="12" sm="6" md="4">
                    <p label="Estado"> Estado: {{ detalleEnsayo.estado }}</p>
                </v-col>
            </v-row>
        </v-card-text>

        <v-divider class="mx-4"></v-divider>

        <v-card-title></v-card-title>

        <v-card-text>
            <template>
                <v-container>
                    <v-row>

                        <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.ensayo" label="Ensayo"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.metodo" label="Metodo"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.tecnica" label="Tecnica"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.valorMinimo" label="Valor Minimo"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.valorMaximo" label="Valor Maximo"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.unidades" label="Unidades"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.costo" label="Costo"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.estado" label="Estado"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.descripcion" label="Descripcion"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.limiteCuantificacion" label="Limite Cuantificacion"></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.titular" label="Titular">
                                </v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="detalleEnsayo.suplente" label="Suplente">
                                </v-text-field>
                              </v-col>


                    </v-row>
                </v-container>
            </template>
        </v-card-text>

        <v-card-actions>
            <v-btn color="deep-purple lighten-2" text to="/Ensayo">
                Regresar
            </v-btn>
            <v-btn color="deep-purple lighten-2" text @click="editarItem(detalleEnsayo._id)">
                Guardar
            </v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
import axios from 'axios';
export default {
    name: "PageInfoEnsayo",

    data: () => ({
        detalleEnsayo: []
    }),
    methods: {
        traerEnsayo() {
            this.detalleEnsayo = this.$store.state.datos;
           

        },
        editarItem(id) {
      console.log(id)
      if (id) {
        axios
          .put(`http://localhost:4701/api/ensayo/edit/item/${id}`, {
            ensayo: this.detalleEnsayo.ensayo,
            metodo: this.detalleEnsayo.temmetodo,
            tecnica: this.detalleEnsayo.tecnica,
            valorMinimo: this.detalleEnsayo.valorMinimo,
            valorMaximo: this.detalleEnsayo.valorMaximo,
            unidades: this.detalleEnsayo.unidades,
            costo: this.detalleEnsayo.costo,
            descripcion: this.detalleEnsayo.descripcion,
            limiteCuantificacion: this.detalleEnsayo.limiteCuantificacion,
            responsable: {
              titular: this.detalleEnsayo.titular,
              suplente: this.detalleEnsayo.suplente
            }

          }
          )
          .then((response) => {

            console.log(response);

          })
          .catch((error) => {
            console.log(error);
          });
      }

    },
    },
    created() {
        this.traerEnsayo()
    }
}
</script>