<template>
    <v-container fluid>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-text-field
          v-model="formato"
          :counter="10"
          :rules="formatoRules"
          label="Formato"
          required
        ></v-text-field>
        <v-text-field
          v-model="codigo"
          :counter="10"
          :rules="codigoRules"
          label="Cdigo"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="aprovacion"
          :counter="10"
          :rules="aprovacionRules"
          label="Aprovacin"
          required
        ></v-text-field>
        <v-text-field
          v-model="version"
          :counter="10"
          :rules="versionRules"
          label="Versin"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="acciones"
          :counter="10"
          :rules="accionesRules"
          label="Acciones"
          required
        ></v-text-field>
  
        <v-checkbox
          v-model="checkbox"
          :rules="[(v) => !!v || 'You must agree to continue!']"
          label="Ests de acuerdo?"
          required
        ></v-checkbox>
        <v-btn :disabled="!valid" color="success" class="mr-4" @click="validate">
          Validar
        </v-btn>
  
        <v-btn color="error" class="mr-4" @click="reset">
          Restablecer formulario
        </v-btn>
  
        <v-btn color="warning" @click="resetValidation">
          Restablecer validacin
        </v-btn>
      </v-form>
    </v-container>
  </template>
  
  <script>
  export default {
      name: "pagesNuevoFormato",
    data: () => ({
  
      valid: true,
      formato: "",
      formatoRules: [
        (v) => !!v || "formato es requerido",
        (v) =>
          (v && v.length <= 10) || "El formato debe tener menos de 10 caracteres",
      ],
  
      valid2: true,
      codigo: "",
      codigoRules: [
        (s) => !!s || "codigo es requerido",
        (s) =>
          (s && s.length <= 10) || "El codigo debe tener menos de 10 caracteres",
      ],
  
      valid3: true,
      aprovacion: "",
      aprovacionRules: [
        (u) => !!u || "provacin es requerido",
        (u) =>
          (u && u.length <= 10) || "El codigo debe tener menos de 10 caracteres",
      ],
  
      valid4: true,
      version: "",
      versionRules: [
        (t) => !!t || "Versin es requerido",
        (t) =>
          (t && t.length <= 10) || "El codigo debe tener menos de 10 caracteres",
      ],
  
      valid5: true,
      acciones: "",
      accionesRules: [
        (a) => !!a || "Acciones es requerido",
        (a) =>
          (a && a.length <= 10) || "El codigo debe tener menos de 10 caracteres",
      ],
    }),
  
    methods: {
      validate() {
        this.$refs.form.validate();
      },
      reset() {
        this.$refs.form.reset();
      },
      resetValidation() {
        this.$refs.form.resetValidation();
      },
    },
  };
  </script>