<template>
    <v-container fluid>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-text-field
          v-model="codigoMuestra"
          :counter="10"
          :rules="codigoMuestraRules"
          label="Cdigo de muestra"
          required
        ></v-text-field>
        <v-text-field
          v-model="municipioRecoleccion"
          :counter="10"
          :rules="municipioRecoleccionRules"
          label="Municipio de recolecn"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="direccion"
          :counter="10"
          :rules="direccionRules"
          label="direccin de toma de muestra"
          required
        ></v-text-field>
        <v-text-field
          v-model="lugar"
          :counter="10"
          :rules="lugarRules"
          label="Lugar de toma de muestra"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="muestra"
          :counter="10"
          :rules="muestraRules"
          label="Muestra recolectada por"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="procedimiento"
          :counter="10"
          :rules="procedimientoRules"
          label="Procedimiento de muestreo"
          required
        ></v-text-field>
        <v-text-field
          v-model="tipo"
          :counter="10"
          :rules="tipoRules"
          label="Tipo de muestra"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="matriz"
          :counter="10"
          :rules="matrizRules"
          label="Matriz de muestra"
          required
        ></v-text-field>
        <v-text-field
          v-model="fechaHora"
          :counter="10"
          :rules="fechaHoraRules"
          label="Fecha y hora de recoleccin"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="cotizacion"
          :counter="10"
          :rules="cotizacionRules"
          label="Cotizacin"
          required
        ></v-text-field>
  
        <v-select
          v-model="select"
          :items="items"
          :rules="[(v) => !!v || 'item es requerido']"
          label="Item"
          required
        ></v-select>
  
        <v-text-field
          v-model="observacion"
          :counter="10"
          :rules="observacionRules"
          label="Observacin"
          required
        ></v-text-field>
  
        <v-checkbox
          v-model="checkbox"
          :rules="[(v) => !!v || 'You must agree to continue!']"
          label="Ests de acuerdo?"
          required
        ></v-checkbox>
        <v-btn :disabled="!valid" color="success" class="mr-4" @click="validate">
          Validar
        </v-btn>
  
        <v-btn color="error" class="mr-4" @click="reset">
          Restablecer formulario
        </v-btn>
  
        <v-btn color="warning" @click="resetValidation">
          Restablecer validacin
        </v-btn>
      </v-form>
    </v-container>
  </template>
  
  <script>
  export default {
    name: "pagesNuevaMuestra",
    data: () => ({
      valid: true,
      codigoMuestra: "",
      codigoMuestraRules: [
        (v) => !!v || "Cdigo de muestra es requerido",
        (v) =>
          (v && v.length <= 10) || "el codigo debe tener menos de 10 caracteres",
      ],
  
      valid2: true,
      municipioRecoleccion: "",
      municipioRecoleccionRules: [
        (s) => !!s || "Municipio de recoleccion es requerido",
        (s) =>
          (s && s.length <= 10) ||
          "Municipio de recoleccion debe tener menos de 10 caracteres",
      ],
  
      valid3: true,
      direccion: "",
      direccionRules: [
        (u) => !!u || "Direccion de toma de muestra es requerido",
        (u) =>
          (u && u.length <= 10) ||
          "la Direccin  debe tener menos de 10 caracteres",
      ],
  
      valid4: true,
      lugar: "",
      lugarRules: [
        (t) => !!t || "Lugar de toma de muestra es requerido",
        (t) =>
          (t && t.length <= 10) ||
          "Lugar de toma de muestra debe tener menos de 10 caracteres",
      ],
  
      valid5: true,
      muestra: "",
      muestraRules: [
        (a) => !!a || "Muestra recolectada por es requerido",
        (a) =>
          (a && a.length <= 10) ||
          "Muestra recolectada por  debe tener menos de 10 caracteres",
      ],
  
      valid6: true,
      procedimiento: "",
      procedimientoRules: [
        (i) => !!i || "Procedimiento de muestreo es requerido",
        (i) =>
          (i && i.length <= 10) ||
          "El Procedimiento de muestreo debe tener menos de 10 caracteres",
      ],
  
      valid7: true,
      tipo: "",
      tipoRules: [
        (o) => !!o || "Tipo de muestra es requerido",
        (o) =>
          (o && o.length <= 10) ||
          "El Tipo de muestra debe tener menos de 10 caracteres",
      ],
  
      valid8: true,
      matriz: "",
      matrizRules: [
        (o) => !!o || "Matriz de muestra es requerido",
        (o) =>
          (o && o.length <= 10) ||
          "Matriz de muestra debe tener menos de 10 caracteres",
      ],
  
      valid9: true,
      fechaHora: "",
      fechaHoraRules: [
        (n) => !!n || "Fecha y hora de recoleccion es requerido",
        (n) =>
          (n && n.length <= 10) ||
          "Fecha y hora de recoleccion  debe tener menos de 10 caracteres",
      ],
  
      valid10: true,
      cotizacion: "",
      cotizacionRules: [
        (k) => !!k || "Cotizacn es requerido",
        (k) =>
          (k && k.length <= 10) || "Cotizacin debe tener menos de 10 caracteres",
      ],
  
      select: null,
      items: ["Item 1", "Item 2", "Item 3", "Item 4"],
  
      valid11: true,
      observacion: "",
      observacionRules: [
        (e) => !!e || "Observacin es requerido",
        (e) =>
          (e && e.length <= 10) || "Observacin debe tener menos de 10 caracteres",
      ],
    }),
  
    methods: {
      validate() {
        this.$refs.form.validate();
      },
      reset() {
        this.$refs.form.reset();
      },
      resetValidation() {
        this.$refs.form.resetValidation();
      },
    },
  };
  </script>