<template>
  <v-app>
    <v-container>
      <v-toolbar color="green" cards dark flat>
          <h3 class="font-italic">REGISTRO ENSAYO</h3>
      </v-toolbar>
      <v-card color="green lighten-5" class="my-8">
        
      <v-form ref="form" v-model="valid" lazy-validation>
       
        <v-text-field
          v-model="ensayo"
          :rules="ensayoRules"
          label="Ensayo"
          outlined 
          class="py-2 px-2"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="metodo"
          :rules="metodoRules"
          label="Mtodo"
          outlined 
          class=" px-2"
          required
        ></v-text-field>
        <v-text-field
          v-model="tecnica"
          :rules="tecnicaRules"
          label="Tcnica"
          outlined 
          class=" px-2"
          required
        ></v-text-field>

        <v-text-field
          v-model="valorMinimo"
          :rules="valorMinimoRules"
          label="Valor Minimo NTC 1311:2019"
          outlined 
          class=" px-2"
          required
        ></v-text-field>
  
        <v-text-field
          v-model="valorMaximo"
          :rules="valorMaximoRules"
          label="Valor Maximo NTC 1311:2019"
          outlined 
          class=" px-2"
          required
        ></v-text-field>
       
        <v-text-field
          v-model="unidades"
          :rules="unidadesRules"
          label="Unidades"
          outlined 
          class=" px-2"
          required
        ></v-text-field>

        <v-text-field
          v-model="costo"
          :rules="unidadesRules"
          label="Costo"
          outlined 
          class=" px-2"
          type="Number"
          required
        ></v-text-field>

        <v-text-field
          v-model="estado"
          :rules="unidadesRules"
          label="Estado"
          outlined 
          class=" px-2"
          type="Number"
          required
        ></v-text-field>

        <v-text-field
          v-model="descripcion"
          label="Descripcion"
          outlined 
          class=" px-2"
          required
        ></v-text-field>

        <v-text-field
          v-model="limiteCuantificacion"
          :rules="unidadesRules"
          label="Limite Cuantificacion"
          outlined 
          class=" px-2"
          type="Number"
          required
        ></v-text-field>

        <v-text-field
          v-model="docTitular"
          label="Documento Titular"
          outlined 
          class=" px-2"
          
        ></v-text-field>

        <v-btn @click="realizarBusqueda()">Traer Titular</v-btn>

        <v-text-field
          v-model="encargado.nombre"
          label="Titular"
          outlined 
          class=" px-2"
          required
        >{{ encargado.nombre }}</v-text-field>

        <v-text-field
          v-model="suplente"
          label="Suplente"
          outlined 
          class=" px-2"
          required
        ></v-text-field>
  
        <!-- <v-checkbox
          v-model="checkbox"
          :rules="[(v) => !!v || 'You must agree to continue!']"
          label="Ests de acuerdo?"
          required
        ></v-checkbox> -->
        <v-btn :disabled="!valid" color="success" class="mr-4" @click="subirEnsayo()">
          Agregar Ensayo
        </v-btn>
  
        <v-btn color="error" class="mr-4" @click="reset">
          Restablecer formulario
        </v-btn>
  
        <v-btn color="warning" @click="resetValidation">
          Restablecer validacin
        </v-btn>
      </v-form>
    
    </v-card>
    </v-container>
  </v-app>
  </template>
  
  <script>
  import axios from 'axios'
  export default {
      name: "pagesFormatoEnsayo",
    data: () => ({
      valid: true,
      fecha: "",
      fechaRules: [
        (v) => !!v || "fecha es requerido",
        (v) =>
          (v && v.length <= 10) || "La fecha debe tener menos de 10 caracteres",
      ],
  
      valid2: true,
      ensayo: "",
      ensayoRules: [
        (s) => !!s || "Ensayo es requerido",
        (s) =>
          (s && s.length <= 10) || "El Ensayo debe tener menos de 10 caracteres",
      ],
  
      valid3: true,
      metodo: "",
      metodoRules: [
        (u) => !!u || "Mtodo es requerido",
        (u) =>
          (u && u.length <= 10) || "El metodo debe tener menos de 10 caracteres",
      ],
  
      valid4: true,
      tecnica: "",
      tecnicaRules: [
        (t) => !!t || "Tcnica es requerido",
        (t) =>
          (t && t.length <= 10) || "La tecnica debe tener menos de 10 caracteres",
      ],
  
      valid7: true,
      valorMaximo: "",
      valorMaximoRules: [
        (o) => !!o || "Valor Maximo es requerido",
        (o) =>
          (o && o.length <= 10) || "El Valor Maximo debe tener menos de 10 caracteres",
      ],
  
      valid8: true,
      valorMinimo: "",
      valorMinimoRules: [
        (o) => !!o || "Valor Mnimo es requerido",
        (o) =>
          (o && o.length <= 10) || "El Valor minimo debe tener menos de 10 caracteres",
      ],
  
      valid9: true,
      unidades: "",
      unidadesRules: [
        (n) => !!n || "Unidades es requerido",
        (n) =>
          (n && n.length <= 10) || "La unidad debe tener menos de 10 caracteres",
      ],
      docTitular:"",
      encargado:[],
      suplente:[],
     costo:"",
     estado:"",
     descripcion:"",
     limiteCuantificacion:"",
     titular:"",
     page:1

    }),
  
    methods: {
      validate() {
        this.$refs.form.validate();
      },
      reset() {
        this.$refs.form.reset();
      },
      resetValidation() {
        this.$refs.form.resetValidation();
      },
      traerUsuario(){
        let docu =this.docTitular
        console.log(this.docTitular)
         axios.get(`http://localhost:4701/api/usuario/cc/nit/${docu}`)
            .then((response)=>{
                 //this.encargado=response.data.ciudad.reduce((obj, item) => (obj[item.nombre] = true, obj), {});
                 this.encargado = response.data.usuario
                
                 console.log(this.encargado)

            })
            .catch((err)=>{
                   console.log(err)
             })

      },
      realizarBusqueda(){
      this.page =1;
      this.traerUsuario();
    },
      subirEnsayo(){
        axios.post("http://localhost:4701/api/ensayo",{
          ensayo:this.ensayo,
          metodo:this.metodo,
          tecnica:this.tecnica,
          valorMinimo: this.valorMinimo,
          valorMaximo:this.valorMaximo,
          unidades:this.unidades,
          costo:this.costo,
          estado:this.estado,
          descripcion:this.descripcion,
          limiteCuantificacion:this.limiteCuantificacion,
          responsable:{
            titular:this.titular,
            suplente:this.suplente
          }


        })
        .then((response)=>{
          console.log(response)
        }
        )
        .catch((err)=>{
          console.log(err)
        })

        

        }
      },
      created(){
        this.page =1;
        
      }

  };
  </script>