<template>
  <v-app>
    <v-container fluid>
      <v-row>
        <v-col class="text-center">
          <v-template>
            <v-toolbar>
              <v-toolbar-title>Ensayos</v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <v-spacer></v-spacer>
              <template>

                <div class="text-center">
                  <v-dialog max-width="800px" v-model="dialog">
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn dark class="mb-2 green lighten-3" v-bind="attrs" v-on="on">
                        Nuevo Ensayo
                      </v-btn>
                    </template>
                    <v-card>
                      <v-card-title class="text-h5 ">Nuevo Ensayo</v-card-title>
                      <v-card-text>
                        <v-row>
                          <v-col cols="12" sm="6" md="6">
                            <v-text-field v-model="ensayo" label="Ensayo" required></v-text-field>
                            <v-text-field v-model="metodo" label="Metodo" required></v-text-field>
                            <v-text-field v-model="tecnica" label="Tecnica" required></v-text-field>
                            <v-text-field v-model="valorMinimo" label="Valor Minimo" required></v-text-field>
                            <v-text-field v-model="valorMaximo" label="Valor Maximo" required></v-text-field>
                            <v-text-field v-model="unidades" label="Unidades" required></v-text-field>
                          </v-col>

                          <v-col cols="12" sm="6" md="6">

                            <v-text-field v-model="costo" label="Costo"></v-text-field>
                            <v-text-field v-model="limiteCuantificacion" label="Limite Cuantificacion" required>
                            </v-text-field>
                            <!-- <v-text-field v-model="documento" label="N. Documento"></v-text-field>
                            <v-btn @click="realizarBusqueda()">Traer usuario</v-btn> -->
                        
                            <v-text-field v-model="titular" label="Titular"></v-text-field>
                            <v-text-field v-model="suplente" label="Suplente"></v-text-field>
                            <v-text-field v-model="descripcion" outlined label="Descripcion" required class="mt-8">
                            </v-text-field>
                          </v-col>
                        </v-row>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close()">Cancel</v-btn>
                        <v-btn color="blue darken-1" text @click="agregarEnsayo()">Guardar</v-btn>
                        <v-spacer></v-spacer>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </div>

              </template>

            </v-toolbar>
          </v-template>
          <template>
            <v-data-table :headers="headers" :items="ensayos" sort-by="nombre" class="elevation-1">
              <template>
                <v-toolbar flat>
                  <v-card>
                    <v-card-title>
                    </v-card-title>

                    <v-card-text>
                      <v-container v-for="item in ensayos" :key="item._id">
                        <v-row>

                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="ensayo" label="Ensayo">{{ item.ensayo }}</v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="metodo" label="Metodo">{{ item.metodo }}</v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="tecnica" label="Tecnica">{{ item.tecnica }}</v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="valorMinimo" label="Valor Minimo">{{ item.valorMinimo }}
                            </v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="valorMaximo" label="Valor Maximo">{{ item.valorMaximo }}
                            </v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="unidades" label="Unidades">{{ item.unidades }}</v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="costo" label="Costo">{{ item.costo }}</v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="estado" label="Estado">{{ item.estado }}</v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="limiteCuantificacion" label="Limite Cuantificacion">{{
                                item.limiteCuantificacion
                            }}</v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6" md="4">
                            <v-text-field v-model="responsable" label="Titular">{{ item.responsable }}</v-text-field>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>
                  </v-card>

                </v-toolbar>
              </template>
              <template v-slot:[`item.acciones`]="{ item }">
                <v-btn color="green" icon dark @click="detalleEnsayo(item)">
                  <font-awesome-icon icon="fa-solid fa-eye" />
                </v-btn>
                <v-btn dark color="green" icon @click="detalleEnsayo(item)">
                  <font-awesome-icon icon="fa-solid fa-pencil" />
                </v-btn>
                <v-btn color="red" icon @click="desactivar(item._id)" v-show="item.estado === 'Activo'">
                  <font-awesome-icon icon="fa-solid fa-ban" />
                </v-btn>
                <v-btn color="green" icon @click="activar(item._id)" v-show="item.estado === 'Inactivo'">
                  <font-awesome-icon icon="fa-solid fa-check" />
                </v-btn>
              </template>
            </v-data-table>

          </template>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>
<script>
import axios from 'axios'
export default {
  name: "pagesEnsayo",
  data: () => ({
    search: '',
    dialogoEdit: false,
    id: '',
    headers: [
      {
        text: 'Ensayo',
        align: 'start',
        sortable: false,
        value: 'ensayo',
      },
      { text: 'Mtodo', value: 'metodo' },
      { text: 'Tcnica', value: 'tecnica' },
      { text: 'Valor Mnimo', value: 'valorMinimo' },
      { text: 'Valor Mximo', value: 'valorMaximo' },
      { text: 'Unidades ', value: 'unidades' },
      { text: 'Costo ', value: 'costo' },
      { text: 'Estado ', value: 'estado' },
      { text: 'Limite Cuantificacion. ', value: 'limiteCuantificacion' },
      { text: 'Responsable ', value: 'responsable' },
      { text: 'Acciones', value: 'acciones', sortable: false },

    ],

    ensayo: "",
    metodo: "",
    tecnica: "",
    valorMinimo: "",
    valorMaximo: "",
    unidades: "",
    costo: "",
    estado: "",
    descripcion: "",
    limiteCuantificacion: "",
    titular: "",
    suplente: "",
    responsable: [],
    documento: "",
    select: null,
    ensayos: [],
    dialog: false,
    usuarios: []
  }),

  methods: {
    detalleEnsayo(item) {
      this.$router.push("/InfoEnsayo");
      this.$store.dispatch("setDatos", item);
      console.log(this.$store.state.datos);
    },
    close() {
      this.dialog = false
    
    },
    // realizarBusqueda() {
    //   this.page = 1;
    //   this.traerUsuario();
    // },
    traerUsuario() {
  
      axios.get(
        "http://localhost:4701/api/usuario/cc/nit")
        .then((response) => {

          this.usuarios = response.data.usuario
          console.log(this.usuarios)
        })
        .catch((error) => {
          console.log(error);
        });

    },
    agregarEnsayo() {
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(header)
      axios
        .post("http://localhost:4701/api/ensayo", {
          ensayo: this.ensayo,
          metodo: this.metodo,
          tecnica: this.tecnica,
          valorMinimo: this.valorMinimo,
          valorMaximo: this.valorMaximo,
          unidades: this.unidades,
          costo: this.costo,
          descripcion: this.descripcion,
          limiteCuantificacion: this.limiteCuantificacion,
          responsable: {
            titular: this.titular,
            suplente: this.suplente
          }



        }, header)
        .then((response) => {
          console.log(response)
        })
        .catch((err) => {
          console.log(err)
        })
    },
    traerEnsayo() {
      let header = { headers: { "x-token": this.$store.state.token } }
      axios.get(
        "http://localhost:4701/api/ensayo", header)
        .then((response) => {
          console.log(response);
          this.ensayos = response.data.ensayo
          console.log(this.ensayos)
        })
        .catch((error) => {
          console.log(error);
        });
    },

    desactivar(id) {
      console.log(id)
      if (id) {
        axios
          .put(`http://localhost:4701/api/ensayo/desactivar/${id}`)
          .then((response) => {
            this.mostrar = false
            console.log(response);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    activar(id) {
      console.log(id)
      if (id) {
        axios
          .put(`http://localhost:4701/api/ensayo/activar/${id}`)
          .then((response) => {
            this.ver = false
            console.log(response);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

  },
  created() {

    this.traerEnsayo(),
    this.traerUsuario()

  },
};
</script>