<template>
  <v-data-table :headers="headers" :items="desserts" sort-by="calories" class="elevation-1">
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Tipos de Muestras</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              Nueva Muestra
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="desserts._id" label="ID">{{ desserts._id}}</v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="desserts.tipos" label="Tipos"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="desserts.estado" label="Estado"></v-text-field>
                  </v-col>


                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">
                Cancel
              </v-btn>
              <v-btn color="blue darken-1" text @click="editarTipo()">
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:[`item.acciones`]="{ item }">
      <v-btn dark color="green" icon @click="editarItem(item._id)">
        <font-awesome-icon icon="fa-solid fa-pencil" />
      </v-btn>
      <v-btn color="red" icon @click="desactivar(item._id)" v-show="item.estado === 'Activo'">
        <font-awesome-icon icon="fa-solid fa-ban" />
      </v-btn>
      <v-btn color="green" icon @click="activar(item._id)" v-show="item.estado === 'Inactivo'">
        <font-awesome-icon icon="fa-solid fa-check" />
      </v-btn>
    </template>
    
  </v-data-table>
</template>
<script>
import axios from 'axios'
export default {
  data: () => ({
    dialog: false,
    dialogoEditar: false,
    type: "",
    state: "",
    id: "",

    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        value: '_id',
      },
      { text: 'Tipos', value: 'tipos' },
      { text: 'Estado', value: 'estado' },
      { text: 'Acciones', value: 'acciones', sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    tipos: "",
    estado: ""
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },

  watch: {
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },

  created() {
    this.initialize()
    this.traerTipos()
    this.agregar()
    //this.editarItem()
  },

  methods: {
    detalleMuestra(item) {

      this.$store.dispatch("setDatos", item);
      console.log(this.$store.state.datos);
    },
    initialize() {
      this.desserts = [
        {
          type: "",
          state: ""
        }
      ]

    },
    traerTipos() {
      let header = { headers: { "x-token": this.$store.state.token } }
      axios.get(
        "http://localhost:4701/api/tipomuestra", header)
        .then((response) => {
          console.log(response);
          this.desserts = response.data.tipoMuestra
          console.log
        })
        .catch((error) => {
          console.log(error);
        });
    },
    desactivar(id) {
      console.log(id)
      if (id) {
        axios
          .put(`http://localhost:4701/api/tipomuestra/desactivar/${id}`)
          .then((response) => {

            console.log(response);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    activar(id) {
      console.log(id)
      if (id) {
        axios
          .put(`http://localhost:4701/api/tipomuestra/activar/tipo/${id}`)
          .then((response) => {

            console.log(response);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    editarItem(id) {
      let header = { headers: { "x-token": this.$store.state.token } }
      if (id) {
        axios
          .put(`http://localhost:4701/api/tipomuestra/${id}`, {
            tipos: this.desserts.tipos,
            estado: this.desserts.estado,


          }, header)
          .then((response) => {
           this.dialog = true
            console.log(response);

          })
          .catch((error) => {
            console.log(error);
          });
      }

    },
    agregar() {
      let header = { headers: { "x-token": this.$store.state.token } }

      axios
        .post("http://localhost:4701/api/tipomuestra", {
          tipos: this.type,
          estado: this.state,


        }, header)
        .then((response) => {

          console.log(response);

        })
        .catch((error) => {
          console.log(error);
        });


    },

    close() {
      this.dialog = false

    },
  },
}
</script>